@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap');


*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-size: 96%;
}

body {
  font-family: 'Montserrat', sans-serif;
  line-height: 1.7;
}


/* Utility classes */

.btnBlue{
  padding: 6px 15px;
  border: none;
  letter-spacing: 2px;
  background-color: #2c5fa7f5;
  color: #fff;
  font-weight: 600;
  cursor: pointer;
  border-radius: 9px;
  font-size: 12px;
}
.normalFont{
  font-size: 1rem;

}
.spantypeFont{
  font-size: 0.8rem;
  color:gray;
  font-weight: 600;
}

header .link{
 font-size: 1rem;
 font-weight: 600;
}


/* Responsive design css */


@media (max-width:700px) {
input.inputelem{
  font-size: 0.8rem;
  }
  div.topSection .btnBlue{
    font-size: 0.8rem;
    padding: 6px 11px;
    font-size: 11px;
  }
  div.topSection{
    width: 100%;
  }
  div.topSection input{
    width: 200px;
  }
  .Wrappedcontainer {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    min-height: 100vh;
    width: 100%;
    padding: 10px;
  }
  .leftSection {
 flex: 1;
    box-shadow: 0px 2px 7px 0px grey;
padding: 10px;
    width: 100%;
    margin: 0px;
    margin-bottom: 10px;
  }
  .middleSection td{
    overflow: hidden;
    padding: 7px;
    font-size: 11px;
  }
  .middleSection th{
    overflow: hidden;
  }
 .middleSection table{
overflow: scroll;
  }
  .RSmainContainer {
      flex:  1;
      display: flex;
      margin-left:0px !important;
      padding: 10px;
      box-shadow: 0px 2px 7px 0px grey;
      flex-direction: column;
      width: 100%;
  }
  .topContainerDiv{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 15px;
  }
  .selectDiv select{
    width: 200px;
  }
  .subContainerLeft{
    margin-bottom: 2rem;
    text-align: center;
  }
  .subContainerRight{
    min-height: 16rem;
    font-size: 7px;
    display: block;
    width: 100%;
  }
  .bottomContainerDiv{
    margin-top: 18rem;
    flex-direction: row;
    flex-wrap: wrap;
  }
  a.link{
    font-size: 13px;
  }
  span.normalFont{
  font-size: 13px;
  width: 150px;
  }
  .detailSpanR{
    height: 30px;
    justify-content: space-evenly;
  }
  .takeMargin {
    width: 6.6rem;
    margin-right: 5rem;
  }
  input.SmallinputBoxes{
    width: 100px;
    margin: 10px 3px;
  }
  input.WidthOfInput,select.WidthOfInput{
    width: 100%;
  }
  input.addInput{
    width: 100%;
    margin: 9px 1px;
    font-size: 0.8rem;
    padding: 8px;
    border-radius: 5px;
    border: 1px solid gray;
  }
  .footerButtons button[type='submit'],
  .footerButtons button[type='reset'],
  .footerButtons button[type='text']{
    width: 70%;
  padding: 10px 0px;
  letter-spacing: 2px;
  font-weight: 700;
  }
  div.footerButtons{
    display: flex;
    flex-direction: column;
  }
  div.slotContainer {
    display: flex;
    justify-content: space-around;
    width: 100%;
    flex-wrap: wrap;
  }
  h1.SlotTime {
    font-size: 22px;
  }
  
  div.slicediv span,
  div.SlotSmallDiv span {
    font-size: 13px;
  }
  
  div.slicediv {
    margin-bottom: 15px;
  }
  
  div.containerThree {
    display: flex;
    width: 100%;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  div.containerThree span {
    font-size: 15px;
    margin-right: 50px;
  }
  
  div.containerThree input[type='date'] {
    padding: 10px 10px;
    width: 200px;
    font-size: 12px;
    margin: 10px 0px;
  }
  
  div.containerThree .btnBlue {
    padding: 9px 12px;
    font-size: 11px;
    width: 100%;
    letter-spacing: 2px;
  }
  
  table,
  td,
  th {
    font-size: 12px;
    overflow: scroll;
    text-align: center;
    border: 1px solid rgba(128, 128, 128, 0.39);
  }
  
  div.containerFour {
    overflow: scroll;
  }
}


