.container{
    display: flex;
    }

.leftSection{
    flex: 2;
    padding: 0.9rem;
  box-shadow: 0px 2px 7px 0px gray;   

}
.topSection{
    display: flex;
    justify-content: space-around;
    align-items:center;
}
.middleSection{
    max-height: 18rem;  
    min-height: 17rem;
    margin: 0.9rem 0rem;
    overflow-y: scroll;
}
table{
    width: 100%;
    text-align: center;
    border:1px solid rgba(128, 128, 128, 0.171);

}
table,th,td{
    border-collapse: collapse;
    padding: 0.7rem;
    font-size: 0.8rem;
    font-weight: 600;
    border-bottom:1px solid rgba(128, 128, 128, 0.171);
    color:rgb(68, 115, 160);
}
th{
    padding: 1.3em;
}

.bottomSection{
    margin: 0.7rem 0rem;
}
.bottomSection::before{
    content:'';
    display: block;
    margin-bottom: 1rem;
    border:1px solid rgba(128, 128, 128, 0.089);
}
.title{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.DetailContainer{
    width: 100%;
    height: 6rem;
    display: flex;
    margin-top: 0.7rem;
    justify-content: space-evenly;
}
.inputelem{
    height: 1.5rem;
    width: 15rem;
    padding: 1rem 1rem;
    border:1px solid gray;  
    border-radius:1rem;
}
.imgdiv{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items:center;
}
.imgdiv img{
width: 3.8rem;
height: 3.7rem;
object-fit:cover;   
border-radius:50%;
}
.Persondetaildiv{
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}
.Persondetaildiv .singlediv{
    width: 100%;
 display: flex;
   justify-content: space-between;
   color:grey;
   font-weight: 600;
   font-size: 0.9rem;
}
.ProfileDetail{
    width: 100%;
}
