.apmntContainerouter {
    padding: 1.2rem 2rem;
    display: flex;
    flex-direction: column;
}

.SlotSmallDiv {
    padding: 0.9rem 1rem;
    color: green;
    font-weight: 600;
    background-color: #fff;
    box-shadow: 1px 1px 2px 1px #808080a3;
    border-radius: 6px;
}

.containerOne,
.containerTwo,
.containerThree,
.containerFour {
    margin: 0.6rem 0px;
}
.containerOne::after{
    content:'';
    display: block;
    margin-top: 1rem;
    border:1px solid rgba(128, 128, 128, 0.089);
}
.containerTwo::after{
    content:'';
    display: block;
    margin-top: 1rem;
    border:1px solid rgba(128, 128, 128, 0.089);    
}
.slicediv span{
    font-size: 1rem;
}
.slicediv {
    margin-right: 0.6rem;
    display: flex;
flex-direction:column;
    flex-wrap: wrap;
}

.slotContainer {
    display: flex;
    text-align: center;
}
.SlotTime {
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 1.5rem;
    font-family: 'Courier New', Courier, monospace;
    color: rgb(67, 123, 226);
}

.containerThree {
    width: 60%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

input[type='date'] {
    padding: 5px;
    border: 1px solid gray;
    border-radius: 4px;
    color: gray;
    font-weight: 600;
    cursor: pointer;
}

.containerThree span {
    font-size: 1.2rem;
}
.containerThree input[type='date']{
padding: 0.6rem 1.5rem;
font-size: 0.9rem;
}   
table {
    border-bottom: 1px solid gray;
    color: rgb(67, 123, 226);
    border-collapse: collapse;
}
.bgBlue{
    background-color:blue;  
}
th {
    padding: 0.6rem;
    margin-bottom: 0.6rem;
}
button.widthbtn{
    padding: 0.5rem 2rem;
}
