.RSmainContainer {
    flex: 4;
    display: flex;
    margin-left: 0.7rem;
    box-shadow: 0px 2px 7px 0px gray;
    flex-direction: column;
    padding: 1.2rem;
    width: 100%;
}
.topContainerDiv {
    display: flex;
    flex-wrap: wrap;    
    border-bottom: 1px solid rgba(128, 128, 128, 0.068);
    width: 100%;
}

.subContainerRight {
    width: 100%;
    flex: 2;
    display: flex;
    height: 249px;  
    padding: 0rem 1.3rem;
    flex-direction: column;
    justify-content: space-around;

}

.subContainerLeft {
    width: 100%;
    padding: 0rem 1.3rem;
    border-right: 1px solid rgba(128, 128, 128, 0.068);
    flex: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
  
}

.selectDiv select,
input.leftinput {
    width: 18rem;
    color: grey;
    border: 1px solid gray;
    border-radius: 5px;
    font-size: 0.8rem;
    padding: 0.6rem 1.1rem;
}

.SelectDivContainer {
    height: 11.2rem;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin-top: 1rem;
}

.detailSpanR {
    display: flex;
    font-size: 0.8rem;
}

.takeMargin {
    width: 9.6rem;
    margin-right: 7rem;
    display: inline-block;
}

.timeselector {
    padding: 0.4rem;
    width: 10rem ;
}

.lastselect {
    width: 75%;
    padding: 0.4rem;
}


/* Bottomdiv style */


.bottomContainerDiv {
    margin-top: 1rem;
}

.BottomMainDiv {
    display: flex;
    padding: 0.2rem 0.9rem;
    flex-direction: column;
    justify-content: space-between;
}

.SmallinputBoxes {
    padding: 0.7rem ;
    width: 6.5rem;
    margin: 0.7rem 0px;
    font-weight: 500;
    color: #808080;
    border: 1px solid gray;
    border-radius: 0.3rem;
}

.WidthOfInput {
    width: 16rem;
}

.SelctDiv {
    color: gray;
    height: 2.5rem;
    border-radius: 5px;
}

.InputBoxContainer {
    display: flex;
    flex-wrap: wrap;
    min-height: 2.8rem;
    padding: 0px;
    justify-content: space-between;
    align-items: center;
}

.ThirdDiv {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}

.footerButtons {
    display: flex;
    justify-content: center;
    align-items: center;
}   
.addInput {
    padding: 0.3rem 0.7rem;
    width: 105px;
    font-size: 0.8rem;
}
button {
    margin: 1.3rem;
}
.btnBlue a {
    text-decoration: none;
    color: #fff;
}